import ReactMarkdown from "react-markdown";

const markdown = `
# MiniDapps (Home Page)

On the Home page, you will find all of your Minima apps. Also known as MiniDapps.

## What are MiniDap​ps?

MiniDapps are Minima’s Decentralized Applications. In other words, applications that run over Minima’s peer-to-peer network.

By using MiniDapps on your device, you can easily:

interact with the Minima blockchain
chat with your contacts over the information layer - Maxima
(in future) transact quickly and cheaply over Omnia - our Layer 2, built for scaling

## What MiniDapps are available?
You will find some MiniDapps, developed by the Minima Team, pre installed.
In future, we expect the majority of MiniDapps to be built by the open-source community - that could be you! 

A brief description of each of them is below.

### 1. Incentive Program

The Incentive Program MiniDapp lets you check your Rewards balance for participating in the Incentive Program. 

Using the Incentive Program MiniDapp, you can:

1. Login to your Incentive Program account

2. Check your Reward Balance and last ping time

3. Copy your Incentive Program ID

4. Share your Incentive Program Invite Link to gain extra Rewards for inviting others

### 2. The Wallet

The Wallet lets you manage the funds you own on the Minima blockchain.
DO NOT SEND FUNDS FROM OTHER CHAINS TO YOUR MINIMA ADDRESS, YOUR FUNDS WILL BE LOST. 

Using the Wallet MiniDapp, you can:

- Check the balance of your Minima or any other Custom Tokens or NFTs you own

- Create your own Custom Tokens and NFTs. Enter a URL ending in .jpg, .jpeg or .png to give your tokens an image

- Share your Minima address with others for receiving $MINIMA, Custom Tokens or NFTs created on the Minima blockchain

- Send $MINIMA, Custom Tokens or NFTs created on the Minima blockchain to other Minima addresses

- Check the Status of your Minima node

### 3. Block Explorer

The Block Explorer lets you view the latest blocks and transactions on the Minima blockchain.

Using the Block Explorer MiniDapp, you can:

- Navigate through the Minima blockchain

- Search by address or TxPoW ID - due to the pruning of the chain, only the last 1000 blocks will be visible

- Select a block to view its details and the transactions in it

- Each block in the chain consists of a main transaction and a transaction list

- When you select a block, you will see 3 tabs, **Details**, **Inputs** and **Outputs**

  **Details:** shows the block details, including its transaction list at the bottom. Select a transaction from the list to see the transaction details.

  **Inputs:** shows details for the coins used as inputs to the block’s main transaction (if one exists).

  **Outputs:** shows the corresponding Outputs of the main transaction (if one exists).

### 4. MaxSolo

MaxSolo is an encrypted, peer-to-peer messaging application, which uses Minima’s information transfer layer, Maxima.

Using MaxSolo you can:

- Send your Maxima Contacts messages, images and $MINIMA coins or Custom Tokens/NFTs created on Minima, that you own

- Experience freedom of information

To use MaxSolo, you must add your Maxima Contacts on the Contacts page.

See the Contacts section to learn more about your Maxima Contacts and how to add them. 

### 5. Script IDE

Script IDE is an Integrated Development Environment for developers to easily write and test Minima scripts.

Using Script IDE, you can: 
- Write, Clean and Save Minima scripts

- Test Minima scripts by running them to determine their result and address

- Script IDE is best used on your Desktop interface. Minima scripts are written in Minima’s KISS scripting language. 

Learn more by following the Transaction tutorial in the Learn Docs MiniDapp. 

### 6. Terminal

The Terminal MiniDapp is a developer app used to execute Minima commands directly on your node without a user interface.

Using the Terminal MiniDapp, you can:
- Check the Status of your node and the blockchain 

- Set and check your Incentive Program Account

- Manually construct and post transactions

- Create and run Minima scripts

- Install and uninstall MiniDapps

Type **help** in the Terminal to see a list of all the commands or visit our docs here

### 7. Learn Docs
The Learn Docs app is where you can learn about the Minima Protocol. You will find our documentation and developer tutorial which introduces Minima’s KISS scripting language for creating smart contracts on Minima.

Our documentation can also be found online [here](https://docs.minima.global/docs/learn/networkoverview).


## How to use MiniDapps
All our MiniDapps come preinstalled, simply click on one to open and start using it.

To upload additional MiniDapps to your Minima app:

**Step 1:** Download a MiniDapp to your phone (Minidapp files have a .minidapp extension)

**Step 2:** Open the Minima app

**Step 3:** On the Home page, click on the **+** button which will open your phone’s file explorer

**Step 4:** Find the downloaded MiniDapp on your phone and select it to install it

**Step 5:** The MiniDapp will appear, select it to open it and start using it!

**Note:** Some MiniDapps will require Testnet Minima coins. Until Mainnet launch, you can request Testnet Minima from an admin in our Discord server. **Testnet Minima have no real world value.**

## Who can build a MiniDapp?
Anyone can learn to build a MiniDapp.

The front-end for MiniDapps can be written using the widely known JavaScript, HTML and CSS. Minima’s KISS scripting language is Turing-Complete, allowing for powerful smart contract driven applications. Building a MiniDapp is accessible to anyone willing to learn!

To learn more about Minima’s scripting language, follow the tutorial found in the Learn Docs MiniDapp.

More documentation for how to build a MiniDapp will be available soon. 

`;

const MiniDappHelp = () => {
  return <ReactMarkdown children={markdown} />;
};
export default MiniDappHelp;
