import { Grid, Button } from "@mui/material";
import { useNavigate } from "react-router-dom";

const HomePage = () => {
  let navigate = useNavigate();

  return (
    <>
      <h2 className="Main-title">Minima Mobile App Help</h2>
      <p className="Main-subtitle">
      Welcome to Minima’s app help <br></br>
      Select a section to learn more
      </p>

      <Grid container spacing={2}>
        <Grid item xs={0} md={4} />
        <Grid item xs={12} md={4}>
          <Grid
            container
            spacing={2}
            justifyContent="center"
            alignItems="center"
          >            
            <Grid item xs={12}>
              <Button
                fullWidth
                onClick={() => navigate("/apphelp/incentiveprogram")}
                sx={mainButtonStyle}
              >
                Incentive Program
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Button
                fullWidth
                onClick={() => navigate("/apphelp/minidapps")}
                sx={mainButtonStyle}
              >
                MiniDapps (Home Page)
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Button
                fullWidth
                onClick={() => navigate("/apphelp/contacts")}
                sx={mainButtonStyle}
              >
                Contacts
              </Button>
            </Grid>
              <Grid item xs={12}>
              <Button
                fullWidth
                onClick={() => navigate("/apphelp/health")}
                sx={mainButtonStyle}
              >
                Health
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Button
                fullWidth
                onClick={() => navigate("/apphelp/backuprestore")}
                sx={mainButtonStyle}
              >
                Backup/Restore
              </Button>
            </Grid>

           </Grid>
        </Grid>
      </Grid>
      <Grid item xs={0} md={4} />
    </>
  );
};

export default HomePage;

const mainButtonStyle = {
  textTransform: "none", // only style that won't apply on mui theme?
  // fontFamily: "Manrope-bold",
  // border: "2px solid #fff",
  // borderRadius: 2,
  // backgroundColor: "transparent",
  // padding: "14px 33px",
  // textTransform: "none",
  // color: "#fff",
  // lineHeight: "140%",
  // fontSize: "18px",
  // ":hover": {
  //   backgroundColor: "#fff",
  //   color: "#16181C",
  //   transform: "scale(0.96)",
  // },
};
