import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Divider,
  Drawer,
  List,
  ListItemButton,
  ListItemText,
  Toolbar,
  Grid,
  Typography,
  Stack,
} from "@mui/material";
import { Box } from "@mui/system";

import { Outlet, useLocation, useNavigate } from "react-router-dom";

import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { ReactComponent as MinimaLogoSquare } from "../assets/icons/minimaLogoSquare.svg";

import { DrawerContentIP } from "./incentiveprogram";
import { DrawerContentMD } from "./minidapps";
import { DrawerContentCP } from "./contacts";
import { DrawerContentH } from "./health";
import { DrawerContentBK } from "./backup";
import { DrawerContentAll } from "./apphelp";

const DefaultDrawerSize = 240;

const PaperContainer = (props: any) => {
  let navigate = useNavigate();
  let location = useLocation();
  let mobileOpen = props.open;
  let handleCloseDrawerToggle = props.handleCloseDrawerToggle;
  const { window } = props;
  const activeRoute = (routeName: string) => {
    return location.pathname.includes(routeName);
  };

  const CustomDrawerListItem = (props: any) => {
    return (
      <ListItemButton
        key={props.pathName + Math.random()}
        onClick={() => {
          navigate(`/${props.section}/${props.pathName}`);
          handleCloseDrawerToggle();
        }}
        selected={activeRoute(`/${props.pathName}`)}
      >
        <ListItemText key={props.pathName} primary={props.name} />
      </ListItemButton>
    );
  };

  const drawer = (
    <div>
      <Toolbar>
        <Stack
          spacing={1}
          direction="row"
          justifyContent="center"
          alignItems="center"
        >
          <MinimaLogoSquare className="App-logo-square" alt="MinimaLogo" />
          <Typography variant="h6" className="App-name">
            App Help
          </Typography>
        </Stack>
      </Toolbar>
      <Divider />
      <List>
        {location.pathname.includes("/apphelp")
            ? DrawerContentAll.map((drawerItem, index) => (
                <CustomDrawerListItem
                  key={index}
                  name={drawerItem.name}
                  pathName={drawerItem.pathName}
                  section="apphelp"
                />
              ))
          : location.pathname.includes("/incentiveprogram")
            ? DrawerContentIP.map((drawerItem, index) => (
                <CustomDrawerListItem
                  key={index}
                  name={drawerItem.name}
                  pathName={drawerItem.pathName}
                  section="incentiveprogram"
                />
              ))
          : location.pathname.includes("/minidapps")
          ? DrawerContentMD.map((drawerItem) => (
              <CustomDrawerListItem
                key={drawerItem.pathName}
                name={drawerItem.name}
                pathName={drawerItem.pathName}
                section="minidapps"
              />
            ))
          : location.pathname.includes("/contacts")
          ? DrawerContentCP.map((drawerItem, index) => (
              <CustomDrawerListItem
                key={index}
                name={drawerItem.name}
                pathName={drawerItem.pathName}
                section="contacts"
              />
            ))
          : location.pathname.includes("/health")
            ? DrawerContentH.map((drawerItem, index) => (
                <CustomDrawerListItem
                  key={index}
                  name={drawerItem.name}
                  pathName={drawerItem.pathName}
                  section="health"
                />
              ))
          : location.pathname.includes("/backup")
          ? DrawerContentBK.map((drawerItem, index) => (
              <CustomDrawerListItem
                key={index}
                name={drawerItem.name}
                pathName={drawerItem.pathName}
                section="backup"
              />
            ))      
          : null}
      </List>
    </div>
  );

  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <>
      <Box
        component="nav"
        sx={{ width: { sm: DefaultDrawerSize, flexShrink: { sm: 0 } } }}
        aria-label="mailbox folders"
      >
        {/* Mobile Drawer */}
        <Drawer
          onBackdropClick={handleCloseDrawerToggle}
          container={container}
          variant="temporary"
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          open={mobileOpen}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: DefaultDrawerSize,
            },
          }}
        >
          {drawer}
        </Drawer>
        {/* Desktop Drawer */}
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: "none", sm: "block" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: DefaultDrawerSize,
            },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box component="main">
        <Grid container>
          <Grid item lg={1} xs={0} />
          <Grid item xs={12} md={12} lg={8}>
            <Outlet />
          </Grid>
        </Grid>
        <Grid item lg={2} xs={0} />
      </Box>
    </>
  );
};

export default PaperContainer;
