import ReactMarkdown from "react-markdown";

const markdown = `
# Node Health

The Health page shows important information about your node.

## Minima
General information about your node and blockchain status.

**Time:** Latest block time.

**Block:** Latest block number.

**Version:** Version of Minima running. This is not the same as the mobile app version.

**RAM:** Amount of memory in use by your node (MB).

**Disk Space:** Amount of disk space in use by your node (MB).

**Devices:** An approximation of the number of active nodes in the network.

**Connections:** Number of peers your node is connected to.

## Maxima
Information about your Maxima Contacts

**Contacts:** Total number of contacts.

**Same Chain:** Number of contacts on the same chain as you.

**Network OK:** Number of contacts you are actively connected to.

## MDS (MiniDapp System)
Information about your Maxima Contacts

**Installed:** Number of MiniDapps installed on your node.

**Desktop Connect:** URL to use on your desktop to interface with your mobile node.

`;

const HealthPage = () => {
  return <ReactMarkdown children={markdown} />;
};

export default HealthPage;