import { useState, useContext } from "react";
import {
  Routes,
  Route,
  useLocation,
  Navigate,
  useNavigate,
} from "react-router-dom";
import {
  Toolbar,
  Grid,
  IconButton,
  Box,
  Stack,
  Typography,
} from "@mui/material";

import HomePage from "./pages/HomePage";
import PaperContainer from "./PaperContainer";

import CottageIcon from "@mui/icons-material/Cottage";
import HomeIcon from "@mui/icons-material/Home";
import MenuIcon from "@mui/icons-material/Menu";

import { ReactComponent as MinimaLogo } from "../assets/icons/minima-logo-landscape.svg";
import { ReactComponent as MinimaLogoSquare } from "../assets/icons/minimaLogoSquare.svg";

/** Incentive Program Content */
//import Overview from "./incentiveprogram/Overview";
import IncentiveHelp from "./apphelp/IncentiveHelp";

/** MiniDapps Content */
//import Home from "./minidapps/Home";
import MiniDappHelp from "./apphelp/MiniDappHelp";

/** Contacts Content */
//import ContactsPage from "./contacts/ContactsPage";
import ContactsHelp from "./apphelp/ContactsHelp";

/** Health Content */
//import HealthPage from "./health/Health";
import HealthHelp from "./apphelp/HealthHelp";

/** Backup Content */
//import Backup from "./backup/Backup";
import BackupHelp from "./apphelp/BackupHelp";

/** Section not Found */
import SectionNotFound from "./SectionNotFound";

import { useTheme } from "@mui/material/styles";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import { ColorModeContext } from "../App";
import Overview from "./incentiveprogram/Overview";


// const ColorModeContext = createContext({ toggleColorMode: () => {} });

// header, footer, main content
// main content has the three butotn page
// WhitePaper with a drop down section listing
// yellowPaper with drop down section listing
// Transaction tutorial
// ?
const AppLayout = () => {
  const theme = useTheme();
  const colorMode = useContext(ColorModeContext);
  const location = useLocation();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);
  const handleCloseDrawerToggle = () => {
    return open ? setOpen(false) : null;
  };

  const handleOpenDrawerToggle = () => {
    return !open ? setOpen(true) : null;
  };

  return (
    // Which header toolbar shall we load?
    <div className="App">
      {location.pathname
        .substring(0, "/apphelp/".length)
        .includes("/apphelp/") ||
      location.pathname
        .substring(0, "/incentiveprogram/".length)
        .includes("/incentiveprogram/") ||
      location.pathname
        .substring(0, "/minidapps/".length)
        .includes("/minidapps/") ||
      location.pathname
        .substring(0, "/contacts/".length)
        .includes("/contacts/") ||
      location.pathname
        .substring(0, "/health/".length)
        .includes("/health/") ||  
      location.pathname
        .substring(0, "/backup/".length)
        .includes("/backup/") ? (
        <Toolbar
          className="App-header App-header-papers"
          sx={{ textAlign: "left" }}
        >
          <Grid container>
            <Grid item lg={1} xs={0}></Grid>
            <Grid item lg={8} xs={12}>
              {location.pathname
                  .substring(0, "/apphelp/".length)
                  .includes("/apphelp/") ? (
                <ToolbarComponent
                  title="App Help"
                  handleOpenDrawerToggle={handleOpenDrawerToggle}
                  navigate={navigate}
                />
             ) : location.pathname
                  .substring(0, "/minidapps/".length)
                  .includes("/minidapps/") ? (
                <ToolbarComponent
                  title="MiniDapps"
                  handleOpenDrawerToggle={handleOpenDrawerToggle}
                  navigate={navigate}
                />
             ) : location.pathname
                .substring(0, "/contacts/".length)
                .includes("/contacts/") ? (
              <ToolbarComponent
                title="Contacts"
                handleOpenDrawerToggle={handleOpenDrawerToggle}
                navigate={navigate}
              /> 
              ) : location.pathname
                .substring(0, "/health/".length)
                .includes("/health/") ? (
              <ToolbarComponent
                title="Health"
                handleOpenDrawerToggle={handleOpenDrawerToggle}
                navigate={navigate}
              /> 
              ) : location.pathname
                  .substring(0, "/backup/".length)
                  .includes("/backup/") ? (
                <ToolbarComponent
                  title="Backup/Restore"
                  handleOpenDrawerToggle={handleOpenDrawerToggle}
                  navigate={navigate}
                />
              ) : location.pathname
                .substring(0, "/incentiveprogram/".length)
                .includes("/incentiveprogram/") ? (
              <ToolbarComponent
                title="Incentive Program"
                handleOpenDrawerToggle={handleOpenDrawerToggle}
                navigate={navigate}
              />
              ) : null}
            </Grid>
            <Grid item lg={2} xs={0}></Grid>
          </Grid>
        </Toolbar>
      ) : (
        <Toolbar className="App-header">
          <Stack
            spacing={1}
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <MinimaLogoSquare className="App-logo-square" alt="MinimaLogo" />
            <Typography variant="h6" className="App-name">
              App Help
            </Typography>
          </Stack>
        </Toolbar>
      )}
      <main
        className={
          location.pathname
            .substring(0, "/apphelp/".length)
            .includes("/apphelp/") ||
          location.pathname
            .substring(0, "/incentiveprogram/".length)
            .includes("/incentiveprogram/") ||
          location.pathname
            .substring(0, "/minidapps/".length)
            .includes("/minidapps/") ||
          location.pathname
            .substring(0, "/contacts/".length)
            .includes("/contacts/") ||
          location.pathname
            .substring(0, "/health/".length)
            .includes("/health/") ||
          location.pathname
            .substring(0, "/backup/".length)
            .includes("/backup/")
            ? "App-content App-content-papers"
            : "App-content App-content-home"
        }
      >
        <Routes>
          <Route path="/" element={<HomePage />}></Route>
          <Route
            path="/apphelp"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route path="" element={<Navigate replace to="incentiveprogram" />} />
            <Route path="incentiveprogram" element={<IncentiveHelp />} />
            <Route path="minidapps" element={<MiniDappHelp />} />
            <Route path="contacts" element={<ContactsHelp />} />
            <Route path="health" element={<HealthHelp />} />
            <Route path="backuprestore" element={<BackupHelp />} />
            <Route path="*" element={<Navigate replace to="incentiveprogram" />} />
          </Route>
          <Route
            path="/backup"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route path="" element={<Navigate replace to="backup" />} />
            <Route path="backup" element={<BackupHelp />} />
            <Route path="*" element={<Navigate replace to="backup" />} />
          </Route>
          <Route
            path="/minidapps"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route
              path="" element={<Navigate replace to="home" />}
            />
            <Route path="home" element={<MiniDappHelp />} />
            <Route path="*" element={<Navigate replace to="home" />}
            />
          </Route>
          <Route
            path="/contacts"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route path="" element={<Navigate replace to="contactspage" />}/>
            <Route path="contacts" element={<ContactsHelp />} />
            <Route path="*" element={<Navigate replace to="contactspage" />} />
          </Route>
          <Route
            path="/health"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route
              path="" element={<Navigate replace to="health" />}
            />
            <Route path="health" element={<HealthHelp />} />
            <Route
              path="*" element={<Navigate replace to="health" />}
            />
          </Route>
          <Route
            path="/incentiveprogram"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route path="" element={<Navigate replace to="overview" />} />
            <Route path="overview" element={<Overview />} />
            <Route path="*" element={<Navigate replace to="overview" />} />
          </Route>
          <Route path="*" element={<SectionNotFound />} />
        </Routes>
      </main>
      {/* Render footer? */}
      {location.pathname
        .substring(0, "/apphelp/".length)
        .includes("/apphelp/") ||
      location.pathname
        .substring(0, "/incentiveprogram/".length)
        .includes("/incentiveprogram/") ||
      location.pathname
        .substring(0, "/minidapps/".length)
        .includes("/minidapps/") ||
      location.pathname
        .substring(0, "/contacts/".length)
        .includes("/contacts/") ||
      location.pathname
        .substring(0, "/health/".length)
        .includes("/health/") ||
      location.pathname
        .substring(0, "/backup/".length)
        .includes("/backup/") ||
      location.pathname
        .substring(0, "/incentiveprogram/".length)
        .includes("/incentiveprogram/") ? null : (
        <footer
          className={`App-footer ${
            theme.palette.mode === "light" ? "App-footer-light" : ""
          }`}
        >
          <Grid container spacing={1} flexDirection="row">
            <Grid item xs={12} md={6}>
              <Stack direction="column" spacing={1}>
                <MinimaLogo className="App-logo" />
                <Typography
                  sx={{
                    fontSize: "calc(8px + 1vmin)",
                  }}
                  variant="subtitle1"
                >
                  The evolution will not be centralized.
                </Typography>
                <Typography
                  sx={{
                    fontFamily: "Manrope-bold",
                    fontSize: "calc(8px + 1vmin)",
                  }}
                  variant="subtitle2"
                >
                  Become part of it.
                </Typography>
              </Stack>
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={0} md={6}></Grid>
            <Grid
              item
              xs={12}
              md={6}
              sx={{
                display: "flex",
                justifyContent: "flex-end",
                alignItems: "flex-start",
              }}
            >
              <Stack direction="row" spacing={1}>
                <IconButton onClick={colorMode.toggleColorMode} color="inherit">
                  {theme.palette.mode === "dark" ? (
                    <Brightness7Icon
                      sx={{ fontSize: "calc(16px + 2vmin)", opacity: 0.8 }}
                    />
                  ) : (
                    <Brightness4Icon
                      sx={{ fontSize: "calc(16px + 2vmin)", opacity: 0.8 }}
                    />
                  )}
                </IconButton>
              </Stack>
            </Grid>
          </Grid>
          <Grid container spacing={1} sx={{ paddingTop: "60px" }}>
            <Grid item xs={12} md={6}>
              <Typography
                sx={{ color: "#767682", fontSize: "calc(8px + 0.5vmin)" }}
                variant="caption"
              >
                All rights reserved Minima Global Ltd ©2022.
              </Typography>
            </Grid>
          </Grid>
        </footer>
      )}
    </div>
  );
};
export default AppLayout;

const ToolbarComponent = (props: any) => {
  return (
    <Box
      component="div"
      sx={{
        display: "flex",
        justifyContent: "space-between",
        flexDirection: "row",
      }}
    >
      <ToolBarTitle
        title={props.title}
        handleOpenDrawerToggle={props.handleOpenDrawerToggle}
      />

      <HomeButton navigate={props.navigate} />
    </Box>
  );
};

const ToolBarTitle = (props: any) => {
  return (
    <Box
      component="div"
      sx={{
        display: "flex",
        justifyContent: "flex-start",
        alignItems: "center",
      }}
    >
      <IconButton
        sx={{
          display: {
            xs: "flex",
            sm: "none",
          },
          paddingLeft: 0,
          alignItems: "center",
          justifyContent: "center",
          "&:hover": {
            backgroundColor: "transparent",
          },
        }}
        color="inherit"
        aria-label="menu"
        onClick={props.handleOpenDrawerToggle}
      >
        <MenuIcon />
      </IconButton>
      {props.title}
    </Box>
  );
};

const HomeButton = (props: any) => {
  return (
    <Box
      component="div"
      color="inherit"
      aria-label="home"
      sx={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
        "&:hover": {
          "#cottageIcon": {
            display: "block",
            cursor: "pointer",
          },
          "#homeIcon": {
            display: "none",
          },
        },
      }}
    >
      <IconButton
        onClick={() => props.navigate("/")}
        sx={{
          color: "#16181C",
          padding: 0,
          "&:hover": {
            backgroundColor: "transparent",
            transform: "scale(1.1)",
            cursor: "pointer",
          },
        }}
      >
        <HomeIcon id="homeIcon" sx={{ fontSize: "calc(16px + 1vmin)" }} />
        <CottageIcon sx={{ display: "none" }} id="cottageIcon" />
      </IconButton>
    </Box>
  );
};
