import ReactMarkdown from "react-markdown";

const markdown = `
# Backup & Restore

It is ESSENTIAL to make regular backups of your Minima node.

Your backup contains your private keys and the proofs for your $MINIMA coins (including Custom Tokens and NFTs). A backup is valid for one month. 

They are your responsibility.

Use the Backup/Restore page to backup your node.

## To make a backup
1. Go to the Backup/Restore page
2. Click **Backup**
3. A backup file will be created and you will be prompted to choose a location

## To restore from a backup
1. Go to the Backup/Restore page
2. Click **Restore**
3. You will be prompted to choose a backup you have previously taken

**Please note:** Your node will be wiped and restored. Anything not contained in your backup will not be there after a restore.

**Store your backups somewhere safe.**

`;

const BackupHelp = () => {
  return <ReactMarkdown children={markdown} />;
};

export default BackupHelp;