import ReactMarkdown from "react-markdown";

const markdown = `
# Incentive Program

## What is the Incentive Program?
The Incentive Program is designed to reward you for running a Minima node during our Testnet. 

It is our way of distributing Minima as widely as possible.

You can also earn additional Rewards by getting your friends to join with your invite link.

If you are not already registered, you can sign up [here](https://incentive.minima.global/account/register) to earn 1 Reward each day your node is connected to the network.

Rewards will be converted on a 1:1 basis with Minima coins at Mainnet launch.

## I’ve registered, how do I start earning?
Once you have registered and logged in, you will see your unique Incentive ID.

To start earning Rewards, you must connect your Incentive ID to your node by following these steps:

1. Log in to the Incentive Program website [here](https://incentive.minima.global/account/login)
2. Copy your Incentive ID
3. Open the Terminal MiniDapp
4. Type the command incentivecash uid:InsertYourNodeID

**Note:** commands must be written in lowercase or they will not work.

You should receive a return status of **True**, and see your Rewards balance and last ping time.

For more details on the Incentive Program, please see our [website](https://docs.minima.global/docs/minimaincentiveprogram/).

## How do I refer a friend to earn extra rewards?
You can earn an additional **10% of a Reward per day** if your friend registers on the Incentive Program using your invite code and successfully runs a Minima node.  
Rewards are paid daily and will only be paid for the days that your referral has their node running, so be sure to help them get set up correctly!

To refer a friend, follow the steps below:

1. Log in to the Incentive Program website [here](https://incentive.minima.global/account/login)
2. Go to the Invite Page and click on Share your Invite Link with your friends
3. Ensure they register and start running a Minima node
4. Ensure they connect their Incentive ID to their Minima node.
5. Start earning additional Rewards each day their node is running!

For more details on the Invite Program, please see our [website](https://docs.minima.global/docs/minimainviteprogram/).

`;

const Overview = () => {
  return <ReactMarkdown children={markdown} />;
};

export default Overview;
