import ReactMarkdown from "react-markdown";

const markdown = `
# Contacts

Contacts is where you create and manage the connections that you want to communicate with over the Maxima network. The Maxima contacts you add will be available for you to chat with in the MaxSolo MiniDapp.

## What are Contacts for?
By adding a friend as Contact, you create a connection with their node across Minima’s peer-to-peer network. This means you will be able to chat and transact with them using the MaxSolo MiniDapp and, in future, any other MiniDapps which use Maxima - the information transfer layer running on Minima’s network.

## What is my Profile?
Your Maxima Profile identifies your Minima node and address in the peer-to-peer network.

By sharing your Profile with your friends and getting them to add you as a Contact, you are allowing them to communicate with you using the MaxSolo MiniDapp.

## How can I share my Profile?
To share your Maxima Profile, click on the **Share** button in the top right of the Contacts page and share it via a platform of your choice.

**Note:** Due to a privacy feature in Maxima, your Profile changes periodically. Ensure that your friends add you as soon as possible after you have shared your Profile.

## How do I add a Contact?

To add Contacts:
1. Ask your friend to share their Profile with you (via a messaging platform of their choice)
2. Copy it
3. Select the + button and paste their Profile in the field
4. Wait a few moments for your Contact to appear
5. Once you have added a Contact, you will automatically appear in their Contact list too

**Note:** Due to a privacy feature in Maxima, Profile identifiers change periodically. Ensure that you add someone's Profile as soon as possible after receiving it.

## What do the lights mean on my Contacts?

For each contact, you will see a Network Status and a Chain Status.

### Network Status

Indicates whether you are successfully connected on the network to your contact.

**Green:** You and your Contact are connected to each other.

**Yellow:** Your node and your Contact’s node have not been able to connect for more than 30 minutes.

**Red:** Your node and your Contact’s node have not been able to connect for more than 1 hour.

### Chain Status

Indicates whether you are on the same chain as your contact.

**Green:** You are on the same chain as your Contact. You both have the same latest block.

**Yellow:** You and your Contact are not on the same latest block.

**Red:** You are not on the same chain as your Contact. You are on different block times.

## How do I fix a yellow or red light?

### Network Status

If the Network Status is **Yellow**, there has been a temporary loss of connection to your Contact.

- Go to the Health page and check the time of your last block, this should be within a few minutes of the current time - see Step 3.
- If your node is online and healthy, your Contact may temporarily be offline and you will be reconnected when they return online.

If the Network Status is **Red**, your connection to your Contact may have been lost. This can happen if you or your Contact are offline for longer than 1 hour.

**Step 1:** Check the Network Status of your other Contacts

- If they are green, this indicates that your Contact’s node is likely offline - ask your Contact to check their node.
- If they are all yellow or red, this indicates a potential problem with your node - see Step 2.

**Step 2:** Go to the Health page

Check the time of your last block, this should be within a few minutes of the current time - see Step 3.

**Step 3:** Open the Terminal MiniDapp (or restart your phone)

In Terminal, type quit, then press Enter. This will force Minima to restart and resync to the blockchain and your Contacts. Alternatively, restart your phone.

**Step 4:** Re add your Contact
If you still cannot get a green light, try re adding your Contact. Ask them to share their Profile, then click on the + button and paste it in there.


### Chain Status

If the **Chain Status** is **Red**, your latest block may not be in sync with your Contacts latest block

**Step 1:** Check the Chain Status of your other Contacts

- If they are green, this indicates that your Contact’s node is likely to be offline - ask your Contact to check their node.
- If they are all yellow or red, this indicates a potential problem with your node - see Step 2.

**Step 2:** Go to the Health page

Check the time of your last block, this should be within a few minutes of the current time - see Step 3.

**Step 3:** Open the Terminal MiniDapp (or restart your phone)

In Terminal, type **quit**, then press Enter. This will force Minima to restart and resync to the blockchain and your Contacts. Alternatively, restart your phone.

**Step 4:** Re add your Contact

If you still cannot get a green light, try re adding your Contact. Ask them to share their Profile, then click on the + button and paste it in there.

`;

const ContactsPage = () => {
  return <ReactMarkdown children={markdown} />;
};

export default ContactsPage;